@import url('https://fonts.googleapis.com/css2?family=Jost:wght@100;200;300;400;500;700;900&display=swap');

$Dark-green: #043e3e;
$Back-green: #02383ab6;
$Orange: #e2d7a6;
$loader:#afa472;
$pink: #d5407d;


* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}


#block {
  display: block;
  height: 100vh;
  width: 100vw;
  top: 0px;
  overflow: hidden;
  position: fixed;
  z-index: 99;
  background-color: $loader;

  #loader {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -0.7em;
    margin-left: -0.7em;
    width: 7.4em;
    height: 7.4em;
    z-index: 100;
    background-color: $loader;
  }

  #hill {
    position: absolute;
    width: 8.1em;
    height: 8.1em;
    top: 2.7em;
    left: 2.7em;
    background-color: transparent;
    border-left: .25em solid whitesmoke;
    transform: rotate(45deg);
  }

  #hill:after {
    content: '';
    position: absolute;
    width: 9.1em;
    height: 9.1em;
    left: 0;
    background-color: $loader;
  }

  #box {
    position: absolute;
    left: 0;
    bottom: -.1em;
    width: 2em;
    height: 2em;
    background-color: transparent;
    border: .25em solid whitesmoke;
    border-radius: 15%;
    transform: translate(0, -1em) rotate(-45deg);
    animation: push 2.5s cubic-bezier(.79, 0, .47, .97) infinite;
  }

  @keyframes push {
    0% {
      transform: translate(0, -1em) rotate(-45deg);
    }

    5% {
      transform: translate(0, -1em) rotate(-50deg);
    }

    20% {
      transform: translate(1em, -2em) rotate(47deg);
    }

    25% {
      transform: translate(1em, -2em) rotate(45deg);
    }

    30% {
      transform: translate(1em, -2em) rotate(40deg);
    }

    45% {
      transform: translate(2em, -3em) rotate(137deg);
    }

    50% {
      transform: translate(2em, -3em) rotate(135deg);
    }

    55% {
      transform: translate(2em, -3em) rotate(130deg);
    }

    70% {
      transform: translate(3em, -4em) rotate(217deg);
    }

    75% {
      transform: translate(3em, -4em) rotate(220deg);
    }

    100% {
      transform: translate(0, -1em) rotate(-225deg);
    }
  }
}


//defaults
body {
  width: 100vw;
  background-color: #fffff0;
  display: block;
  overflow-x: hidden;
  font-family: 'Jost', sans-serif;
}

.sectext {
  z-index: 1;
  font-size: 3em;
  font-weight: 400;
  background-color: $Dark-green;
  padding-right: 20px;
  padding-left: 20px;
  font-family: 'Jost', sans-serif;
  color: ivory;
  opacity: 0.5;
}

.sectext::first-letter {
  font-size: 3.5rem;
}

.container {
  position: relative;

}

.bg {
  z-index: -1;
  opacity: 0.2;
  color: $Dark-green;
  position: absolute;

  font-family: 'Jost', sans-serif;
  font-size: 15rem;
}



.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

//Section1
.section1 {
  height: 100vh;
  width: 100vw;
  position: relative;

  .scroll {
    position: absolute;
    left: 24%;
    bottom: 7vh;
    opacity: 0;
  }

  .flexsec1 {
    height: 100%;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: row;
  }
}

.mainimg {
  height: 80vh;
  width: auto;
  opacity: 1;
  z-index: -1;
  object-fit: cover;
  align-self: center;
  justify-self: left;
}

.sec1text {
  font-family: 'Jost', sans-serif;
  font-size: 6em;
  font-weight: 400;
  z-index: 1;
  text-align: center;
  color: #043e3e;
  width: 55%;
  align-self: center;
  line-height: 50px;

  .rahul {
    display: inline-block;
    opacity: 0;
    transform: translateX(-100px);
    font-weight: 400;
  }

  .rahul::after {
    content: "";
    width: 100%;
    height: 15px;
    background-color: #d5407e7a;
    display: block;
    position: absolute;
    bottom: -8px;
    left: 15px;
    z-index: -1;
    opacity: .4;
  }

  .srname {
    font-size: 2.6rem;
    display: inline-block;
    opacity: 0;
    margin-left: 25%;
    transform: translateX(-100px);
    margin-bottom: 40px;
  }

  .discripton {
    font-size: 1.2rem;
    font-weight: 300;
    display: block;
    line-height: 1;
    overflow: hidden;
    opacity: 0;
    transform: translateX(-100px);
  }

  .links {
    color: $pink;
    display: inline-block;
    font-size: 1.4rem;
    opacity: 0;
    transform: translateX(-100px);

    a {
      text-decoration: none;
    }
  }

}

