      opacity: 0.6;
    }
  }

}

.laptop {
  position: relative;
  left: -15%;
  background-image: url("/img/Qarothome.jpg");
  background-position: -1vw 0%;
  width: 70%;
  height: 88%;
  background-size: cover;
  background-repeat: no-repeat;
  border-top-right-radius: 5%;
}

.iphone {

  margin-right: 6%;
  height: 45vh;
  width: auto;
}

.sec2text {
  position: absolute;
  bottom: 40%;
  left: 50%;
}


.ouro {
  position: relative;
  display: inline-block;
  height: 46px;
  width: 46px;
  margin: 1em;
  border-radius: 50%;
  background: none repeat scroll 0 0 #02383aa4;
  overflow: hidden;
  transform: scale(var(--ggs, 2.8));
  cursor: pointer;
}

.ouro:after {
  content: "";
  position: absolute;
  top: 9px;
  left: 9px;
  display: block;
  height: 28px;
  width: 28px;
  background: none repeat scroll 0 0 #fffff0;
  border-radius: 50%;
}

.ouro>span {
  position: absolute;
  height: 100%;
  width: 50%;
  overflow: hidden;
}

.left {
  left: 0
}

.right {
  left: 50%
}

.anim {
  position: absolute;
  left: 100%;
  top: 0;
  height: 100%;
  width: 100%;
  border-radius: 999px;
  background: none repeat scroll 0 0 $Dark-green;
  opacity: 0.6;
  -webkit-animation: ui-spinner-rotate-left 3s 5;
  animation: ui-spinner-rotate-left 3s 5;
  -webkit-transform-origin: 0 50% 0;
  transform-origin: 0 50% 0;
}

.left .anim {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}

.right .anim {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  left: -100%;
  -webkit-transform-origin: 100% 50% 0;
  transform-origin: 100% 50% 0;
}

/* v2 */
.ouro2 .anim {
  -webkit-animation-delay: 0;
  animation-delay: 0;
}

.ouro2 .right .anim {
  -webkit-animation-delay: 1.5s;
  animation-delay: 1.5s;
}


/* round variation */
.round .ouro:after {
  display: none
}


@keyframes ui-spinner-rotate-right {
  0% {
    transform: rotate(0deg)
  }

  25% {
    transform: rotate(180deg)
  }

  50% {
    transform: rotate(180deg)
  }

  75% {
    transform: rotate(360deg)
  }

  100% {
    transform: rotate(360deg)
  }
}

@keyframes ui-spinner-rotate-left {
  0% {
    transform: rotate(0deg)
  }

  25% {
    transform: rotate(0deg)
  }

  50% {
    transform: rotate(180deg)
  }

  75% {
    transform: rotate(180deg)
  }

  100% {
    transform: rotate(360deg)
  }
}

@-webkit-keyframes ui-spinner-rotate-right {
  0% {
    -webkit-transform: rotate(0deg)
  }

  25% {
    -webkit-transform: rotate(180deg)
  }

  50% {
    -webkit-transform: rotate(180deg)
  }

  75% {
    -webkit-transform: rotate(360deg)
  }

  100% {
    -webkit-transform: rotate(360deg)
  }
}

@-webkit-keyframes ui-spinner-rotate-left {
  0% {
    -webkit-transform: rotate(0deg)
  }

  25% {
    -webkit-transform: rotate(0deg)
  }

  50% {
    -webkit-transform: rotate(180deg)
  }

  75% {
    -webkit-transform: rotate(180deg)
  }

  100% {
    -webkit-transform: rotate(360deg)
  }
}


//Section3

.section3 {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;

  .sec3img1 {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.11),
      0 2px 2px rgba(0, 0, 0, 0.11),
      0 4px 4px rgba(0, 0, 0, 0.11),
      0 6px 8px rgba(0, 0, 0, 0.11),
      0 8px 16px rgba(0, 0, 0, 0.11);
    background-position-x: 0px;
    background-image: url("/img/1.jpg");
    overflow: hidden;
    height: 60vh;
    width: 40vh;
    border-radius: 3px;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .sec3img2 {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25),
      0 2px 2px rgba(0, 0, 0, 0.20),
      0 4px 4px rgba(0, 0, 0, 0.15),
      0 8px 8px rgba(0, 0, 0, 0.10),
      0 20px 30px -15px rgba(87, 87, 80, 0.5),
      0 40px 40px -20px rgba(88, 87, 82, 0.4);
    background-position-x: 0px;
    background-image: url("/img/1c.jpg");
    overflow: hidden;
    height: 60vh;
    width: 40vh;
    border-radius: 3px;
    background-repeat: no-repeat;
    background-size: cover;
    align-self: flex-end;
  }

  .sec3next {
    height: 18vh;
    width: 18vh;
    cursor: pointer;
    transition: 0.1s transform;
  }

  .sec3next:hover {
    transform: scale(1.1, 1.1);
  }

  .sec3next:active {
    transform: scale(0.8, 0.8);
  }

  .bg {
    left: 55%;
    top: 5%;
  }
}

.sec3img {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  height: 100%;
  width: 73vw;
  margin-left: 6vw;

}

.fleximgsec3 {
  display: flex;
  flex-direction: column;
  width: fit-content;
  width: 50%;

  img {
    align-self: flex-end;
    height: 55vh;
    width: auto;
  }

  p {
    align-self: flex-start;
    width: 110%;
    background-color: #e2d7a6;
    font-family: 'Jost', sans-serif;
    font-size: 1.2em;
    padding: 5%;
    margin: 5%;
  }
}

.sec3img img:last-child {
  align-self: center;

}

.sec3text {
  position: absolute;
  left: 25%;
  top: 50%;
}



