//section5
.section5 {

  position: relative;
  min-height: 100vh;
  margin-bottom: 20px;
  /* *****  GALLERIES ***** */

  .gallery-section{
    width: 100%;
    padding: 60px 0;
  }
  
  .inner-width{
    width: 100%;
    max-width: 1200px;
    margin: auto;
    padding: 0 20px;
  }
  

  
  .gallery-section .gallery{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-auto-rows: auto;
    grid-gap: 1rem;
    grid-auto-flow: dense;
  }
  
  .gallery-section .image{
    overflow: hidden;
    cursor: pointer;
  }
  
  .gallery-section .image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: 0.4s;
    filter: saturate(0);
  }
  
  .gallery-section .image:hover img{
    filter: saturate(1);
  }
  

  @media screen and (max-width:768px) {
    .gallery-section .gallery{
      grid-template-columns: repeat(3, 1fr);
      gap: 2rem;
    }
  }
  

  
 

  .w-1 {
    grid-column: span 1;
  }

  .w-2 {
    grid-column: span 2;
  }

  .w-3 {
    grid-column: span 3;
  }

  



  .h-1 {
    grid-row: span 1;
  }

  .h-2 {
    grid-row: span 2;
  }

  .h-3 {
    grid-row: span 3;
  }


  @media screen and (max-width:480px) {
    .gallery-section .gallery{
      // display: flex;
      // flex-direction: column;
      // gap: 2rem;
      // width: 70%;
      // margin-left: 15%;
      grid-template-columns: repeat(2, 1fr);
      gap: 5px;
      width: 80%;
      margin-left: 10%;
    }
    .w-3{ grid-column: span 2;}

    .center{
      font-size: 3em;
    }
  }

  .sec5text {
    position: absolute;
    left: 8%;
    opacity: 0.4;
    top: 45%;
    z-index: 6;
  }

  .bg {
    right: 5%;
  }

}



